



































































import Vue from 'vue';
import { Skill } from 'ironquest';
import { capitalize, map } from 'lodash';
import { mapState } from 'vuex';
import { mapFields } from '@/lib';

const SKILLS = [
  Skill.AGILITY,
  Skill.ATTACK,
  Skill.CONSTITUTION,
  Skill.CONSTRUCTION,
  Skill.COOKING,
  Skill.CRAFTING,
  Skill.DEFENCE,
  Skill.DIVINATION,
  Skill.DUNGEONEERING,
  Skill.FARMING,
  Skill.FIREMAKING,
  Skill.FISHING,
  Skill.FLETCHING,
  Skill.HERBLORE,
  Skill.HUNTER,
  Skill.INVENTION,
  Skill.MAGIC,
  Skill.MINING,
  Skill.PRAYER,
  Skill.RANGED,
  Skill.RUNECRAFTING,
  Skill.SLAYER,
  Skill.SMITHING,
  Skill.STRENGTH,
  Skill.SUMMONING,
  Skill.THIEVING,
  Skill.WOODCUTTING,
] as Skill[];

export default Vue.extend({
  name: 'settings',
  computed: {
    skillOptions() {
      return map(SKILLS, skill => {
        return {
          value: skill,
          text: capitalize(skill),
        };
      });
    },
    ...mapFields([
      'parameters.name',
      'parameters.typeFilter',
      'parameters.accessFilter',
      'parameters.ironman',
      'parameters.recommended',
      'parameters.lampSkills',
    ]),
  },
});
